@charset "utf-8";

body {
    font-family: "나눔바른고딕",NanumBarunGothic,"나눔고딕",NanumGothic,"맑은 고딕","Malgun Gothic", Dotum,'돋움', "Helvetica Neue", Helvetica, Arial, sans-serif;
position: relative;
          padding-top: 3vh;
}

#canvas {}
#canvas-svg { width:100%; }
#share { margin:2vh 0; float:right; }

.land { fill: #222; }

.boundary {
    fill: none;
    stroke: #fff;
    stroke-width: 1px;
}

#tooltip-container {
    position: absolute;
    background-color: #fff;
    color: #000;
    padding: 10px;
    border: 1px solid;
    display: none;
}

.tooltip_key {
    font-weight: bold;
}

.tooltip_value {
    margin-left: 20px;
    float: right;
}

.loading {
    margin: 10vh auto;
    text-align: center;
}

